<template>
	<div class="my-3 p-3 shadow-sm border d-flex justify-content-between">
		<div class="d-flex w-100">
			<span class="mx-2 d-flex align-items-center col-2">
				<i class="fa fa-filter" aria-hidden="true"></i>
				<span class="mx-2" style="width: 15vh">{{
					$t("inventory_search_bar_filter_button")
				}}</span>
			</span>
			<div class="col-10 d-flex align-items-center px-2">
				<div class="col-md-8 col-sm-12 border d-flex">
					<input
						type="search"
						placeholder="Search"
						style="border: none"
						class="p-2 w-100"
						v-model="SearchQuery"
						@keydown.enter="SearchProduct"
					/>
					<select
						class="form-group"
						style="border: none"
						v-model="SearchType"
					>
						<option value="product">Products</option>
						<option value="category">Categories</option>
					</select>
					<button
						class="
							btn
							border
							theme-background-primary
							text-light
							p-2
						"
						@click="SearchProduct"
					>
						{{ $t("inventory_search_bar_button_name") }}
					</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: "ProductSearch",
	props: {
		SearchQuery: {
			type: String,
			default: "",
		},
		SearchType: {
			type: String,
			default: "product",
		},
	},
	methods: {
		SearchProduct() {
			this.$emit("search_product", {
				SearchQuery: this.SearchQuery,
				SearchType: this.SearchType,
			});
		},
	},
};
</script>